.save-autofill-page .personal-info .row1{
    margin: 5% 0;
    padding:0 7.5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .col1{
        width: 40%;
        position: relative;
        &:hover{
            .top-img{
                transform: scale(1.035);
                transition: 0.35s ease-in-out;
            }
        }
    }
    .col2{
        width: 40%;
    }
    .bg-img{
        content: url('../../../../assets/SaveAutofill/shapes.png');
        width: 100%;
    }
    .top-img{
        content: url('../../../../assets/SaveAutofill/img1.png');
        width: 100%;
        position: absolute;
        top:10%;
    }
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .save-autofill-page .personal-info .row1{        
        h1{
            font-size: 30px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .save-autofill-page .personal-info .row1{
        .col1{
            width: 45%;
        }
        .col2{
            width: 47.5%;
        }
    }
}

@media(max-width:767px){
    .save-autofill-page .personal-info .row1{
        margin: 30px 0;
        display: block;
        .col1{
            width: 425px;
            max-width: 90%;
            margin:25px auto;
        }
        .col2{
            width: 425px;
            max-width: 100%;
            margin:25px auto;
            text-align: center;
        }
        .top-img{
            top:10%;
        }
        h1{
            font-size: 20px;
        }
    }
}

//==============================================
.save-autofill-page .personal-info .row2{
    padding:0 7.5%;
    margin:7.5% 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    .col1{
        position: relative;
        width: 60%;
    }
    .bg-img{
        content: url('../../../../assets/SaveAutofill/bg.png');
        width: 100%;
    }
    .info{
        width: 50%;
        position: absolute;
        left:10%;
        top:35%;
        color:#fff;
    }
    h1{
        font-size: 32.5px;
    }
    p{
        display: flex;
        align-items: center;
        font-size: 18px;
        margin:35px 0 45px;
        svg{
            font-size: 22px;
            margin-right: 10px;
        }
    }
    a{
        border:1px solid #fff;
        color:#fff;
        font-size: 17px;
        padding:12px 30px;
        border-radius: 25px;
    }
    .top-img{
        content: url('../../../../assets/SaveAutofill/dashboard.png');
        width: 45%;
        position: absolute;
        right: 7.5%;
        top:15%;
        border-radius: 15px;
    }
}
@media(max-width:1401px){
    .save-autofill-page .personal-info .row2{
        .info{
            left:10%;
            top:20%;
        }
        h1{
            font-size: 27.5px;
        }
        p{
            font-size: 15px;
            margin:30px 0 40px;
            svg{
                font-size: 17.5px;
            }
        }
        a{
            font-size: 14px;
            padding:10px 25px;
            border-radius: 25px;
        }
    }
}

@media(max-width:991px){
    .save-autofill-page .personal-info .row2{
        margin:40px 0;
        display: block;
        .col1{
            width: 450px;
            max-width: 100%;
            margin:25px auto;
        }
        .info{
            width: 80%;
            left:50%;
            top:50%;
            transform: translate(-50%, -50%);
        }
        h1{
            font-size: 20px;
        }
        .top-img{
            width: 450px;
            max-width: 100%;
            margin:25px auto;
            position: relative;
            right: unset;
            top:unset;
        }
    }
}

@media(max-width:470px){
    .save-autofill-page .personal-info .row2{
        p{
            margin:15px 0 25px;
        }
        .info{
            width: 87.5%;
        }
        h1{
            font-size: 17.5px;
        }
        a{
            padding:7.5px 20px;
        }
    }
}

@media(max-width:335px){
    .save-autofill-page .personal-info .row2{
        p{
            margin:10px 0 20px;
        }
        h1{
            font-size: 15px;
        }
    }
}

.save-autofill-page .personal-info .row2{
    a{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        background: transparent;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    a:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        bottom: -1px;
        left: -1px;
        right: -1px;
        border-radius: 25px;
        background: #fff;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 50%;
        transform-origin: 50%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    a:hover, a:focus, .a:active {
        color: #427cff;
        border:1px solid transparent;
        transition: 0.25s ease-in-out;
    }
    a:hover:before, a:focus:before, a:active:before {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
      
}