.save-autofill-page .features .row3{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:10% 0 7.5%;
    padding:0 10%;
    .col1{
        width: 30%;
        p{
            font-size: 18px;
        }
        h1{
            font-size: 35px;
            margin-bottom: 20px;
        }
    }
    .col2{
        width: 55%;
    }
    .item1{
        width: 75%;
        border:1px solid #eee;
        box-shadow: rgba(0, 0, 0, 0.05) 0px 4px 5px 0px, rgba(0, 0, 0, 0.06) 0px 2px 3px -1px;
        display: flex;
        align-items: center;
        padding:20px 45px;
        border-radius: 10px;
        p{
            font-size: 18px;
            flex: 1;
        }
        .icon{
            width: 50px;
            margin-right: 20px;
        }
        &:first-child{
            margin:25px 0;
        }
        &:nth-child(2){
            margin:25px auto;
        }
        &:last-child{
            margin: 25px 0 25px auto;
        }
    }
}

@media(max-width:1401px){
    .save-autofill-page .features .row3{
        .col1{
            p{
                font-size: 15px;
            }
            h1{
                font-size: 30px;
                margin-bottom: 15px;
            }
        }
        .item1{
            box-shadow: rgba(0, 0, 0, 0.1) 2px 2px 3px;
            padding:15px 35px;
            p{
                font-size: 15px;
            }
            .icon{
                width: 45px;
                margin-right: 15px;
            }
            &:first-child{
                margin:20px 0;
            }
            &:nth-child(2){
                margin:20px auto;
            }
            &:last-child{
                margin: 20px 0 20px auto;
            }
        }
    }
}

@media(max-width:991px){
    .save-autofill-page .features .row3{
        padding:0 7.5%;
        .col1{
            width: 35%;
        }
        .col2{
            width: 57.5%;
        }
        .item1{
            width: 80%;
            padding: 15px 25px;
        }
    }
}

@media(max-width:767px){
    .save-autofill-page .features .row3{
        display: block;
        margin:45px 0 30px;
        .col1{
            width: 100%;
            margin:20px 0;
            h1{
                font-size: 20px;
            }
        }
        .col2{
            width: 100%;
            margin:20px 0;
        }
        .item1{
            width: 100%;
            box-shadow: none;
            &:first-child{
                margin:15px 0;
            }
            &:nth-child(2){
                margin:15px 0;
            }
            &:last-child{
                margin: 15px 0;
            }
            .icon{
                width: 35px;
            }
        }
    }    
}

//=========================================
.save-autofill-page .features .row4{
    padding:3.5% 7.5%;
    margin:7.5% 0;
    background-color: #fcfcfc;
    h1{
        font-size: 35px;
        text-align: center;
        margin-bottom: 100px;
    }
    .items2{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item2{
        width: 22.5%;
        position: relative;
        background-color: #fff;
        border:1px solid transparent;
        border-radius: 10px;
        padding:75px 35px 35px;
        transition: 0.3s ease-in-out;
        &:hover{
            transform: scale(1.035);
            border:1px solid #eee;
            .icon{
                background-color: #427cff;
                transition: 0.2s ease-in-out;
            }
            svg{
                color:#fff;
                transition: 0.2s ease-in-out;
            }
        }
    }
    .icon{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 86px;
        height: 86px;
        border-radius: 50%;
        position: absolute;
        left:35px;
        top:-43px;
        background: #fff;
    }
    svg{
        font-size: 50px;
        color:#427cff;
    }
    h6{
        font-size: 18px;
        margin-bottom: 15px;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .save-autofill-page .features .row4{
        h1{
            font-size: 30px;
            margin-bottom: 75px;
        }
        .item2{
            padding:65px 25px 25px;
        }
        .icon{
            width: 70px;
            height: 70px;
            left:25px;
            top:-35px;
        }
        svg{
            font-size: 35px;
        }
        h6{
            font-size: 15px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .save-autofill-page .features .row4{
        h1{
            margin-bottom: 35px;
        }
        .item2{
            width: 47.5%;
            margin:30px 0;
        }
    }
}

@media(max-width:500px){
    .save-autofill-page .features .row4{
        padding:30px 7.5%;
        margin:50px 0 30px;
        h1{
            font-size: 20px;
        }
        .item2{
            width: 100%;
        }
    }
}

//====================================================
@-webkit-keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
}
@keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
}

.save-autofill-page .features .row3{
    .item1{
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        &:hover{
            transform: scale(1.025);
        }
    }
    .item1 .icon{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
    .item1:hover .icon{
        -webkit-animation-name: hvr-icon-wobble-vertical;
        animation-name: hvr-icon-wobble-vertical;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        animation-delay: 0.3s;
        -webkit-animation-delay: 0.3s;
        -webkit-animation-timing-function: ease-in-out;
        animation-timing-function: ease-in-out;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
    }
}