.save-autofill-page{
    overflow: hidden;
}
.save-autofill-page .shared-banner{
    clip-path: polygon(0 0, 100% 0, 100% 85%, 71% 100%, 0 85%);
    position: relative;
    overflow:visible;
    z-index: 1;
}
@media(max-width:767px){
    .save-autofill-page .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 90%, 71% 100%, 0 90%);
    }
}

.save-autofill-page .shared-banner .other-elements{
    position: absolute;
    right:10%;
    bottom:5%;
    width: 35%;
    z-index: 2;
    .banner-img, .inner-img{
        display: relative;
        width:100%
    }
    .bg-img{
        content: url('../../../../assets/SaveAutofill/banner-img.png');
        width: 100%;
    }
    .data-container{
        background-color: #fff;
        border:1px solid #ddd;
        position: absolute;
        width: 80%;
        left:15%;
        padding: 35px;
        border-radius: 15px;
        bottom:5%;
    }
    .inline{
        display: flex;
        align-items: center;
    }
    .user{
        content: url('../../../../assets/SaveAutofill/user.png');
        width: 55px;
        margin-right: 15px;
    }
    h4{
        font-size: 19px;
        flex: 1;
    }
    .input{
        margin:17.5px 0;
    }
    small{
        color:#555;
        font-size: 17px;
    }
    h6{
        font-size: 16px;
        margin-top: 5px;
    }
    h6,h4{
        color:#111;
    }
}

@media(max-width:1401px){
    .save-autofill-page .shared-banner .other-elements{
        .data-container{
            padding: 20px;
        }
        .user{
            width: 40px;
            margin-right: 10px;
        }
        h4{
            font-size: 16px;
        }
        .input{
            margin:10px 0;
        }
        small{
            font-size: 14px;
        }
        h6{
            font-size: 13px;
            margin-top: 5px;
        }
    }
}

@media(max-width:880px){
    .save-autofill-page .shared-banner .other-elements{
        position: relative;
        right:unset;
        bottom:unset;
        width: unset;
        width: 500px;
        max-width: 100%;
        margin:50px auto;
        .data-container{
            padding: 20px;
            border-radius: 10px;
            bottom:-70px;
        }
    }
}

@media(max-width:350px){
    .save-autofill-page .shared-banner .other-elements{
        .data-container{
            width: 100%;
            left:unset;
            right: -5%;
            bottom:-85px;
        }
    }
}


.save-autofill-page .shared-banner{
    .data-container{
        overflow: hidden;
      }
      .data-container::before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: #427cff;
        height: 4px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
      }
    .data-container:hover:before{
        left: 0;
        right: 0;
    }
}